.paginator {
    max-width: 1600px;
    display: flex;
    /* flex-wrap: wrap; */
    overflow-x: scroll;
    margin-top: auto;
}
.number {
    font-size: 30px;
    margin: 2px 10px;
    padding: 0px 6px;
    border-radius: 3px;
    cursor: pointer;
    color: white;
    /* background-color: rgb(23, 184, 144); */
}
.activeNumber {
    background-color: rgb(23, 184, 144);
    color: black;
    font-size: 30px;
    margin: 2px 10px;
    padding: 0px 6px;
    border-radius: 3px;
    cursor: pointer;
}