.Main {
    min-height: 100%;
    padding: 30px 0px;
    border-top: 1px solid rgb(38, 42, 47);
    position: relative;
    flex: 1;
}   

.container {
    display: flex;
    flex-direction: column;
    gap: 32px;
    max-width: 1600px;
    margin: 0px auto;
    padding: 0px 10px;
    flex: 1;
}

.title {
    font-size: 36px;
    font-weight: 600;
    line-height: 44px;
    /* margin-left: 65px; */
    margin: 0px auto;
    flex: 1;
    text-align: center;
}



.items {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-wrap: wrap;
    gap: 16px;
}
.item {
    
}
@media (max-width: 1600px) {
    .container {
        max-width: 1400px;
    }
}
@media (max-width: 1400px) {
    .container {
        max-width: 1200px;
    }
}
@media (max-width: 1200px) {
    .container {
        max-width: 1000px;
    }
}
@media (max-width: 1000px) {
    .container {
        max-width: 800px;
    }
}
@media (max-width: 800px) {
    .container {
        max-width: 650px;
    }
}
@media (max-width: 650px) {
    .container {
        max-width: 450px;
    }
}

.notFoundTitle {
    font-size: 60px;
    font-weight: 600;
    margin: 50px auto;
}