

.customScrollbar {
    width: 300px; /* Ширина контейнера */
    height: 400px; /* Высота контейнера */
    overflow-y: scroll; /* Включение вертикального скроллбара */
  }
  
  .customScrollbar::-webkit-scrollbar {
    width: 10px; /* Ширина скроллбара */
  }
  
  .customScrollbar::-webkit-scrollbar-thumb {
    background-color: #f40707; /* Цвет ползунка скроллбара */
  }

.ItemCard {
    position: fixed;
    top: 10%;
    right: 1%;
    width: 400px;
    max-height: 600px;
    background-color: rgb(26, 38, 57);
    padding: 10px;
    padding-bottom: 20px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    border-radius: 15px;
    overflow: auto;
}

.image {
    width: 305px;
    height: 425px;
    border-radius: 10px;
    margin-bottom: 10px;
}
.title {
    font-size: 30px;
    font-weight: 600;
    line-height: 36px;
}
.genres {
    color: rgb(113, 117, 122);
    font-size: 18px;
    font-weight: 500;
    line-height: 22px;
    margin-bottom: 10px;
    display: flex;
    flex-wrap: wrap;
    gap: 6px;
    max-width: 350px;
}
.genrePoint {
    color: aliceblue;
}
.genre {

}
.genre:last-child .genrePoint{
    display: none;

}
.synopsys {
    margin-bottom: 20px;
}
.moreInfo {
    cursor: pointer;
    color: rgb(23, 184, 144);
}
.closeButtonIcon {
    cursor: pointer;
    position: absolute;
    width: 30px;
    height: 30px;
    top: 10px;
    right: 10px;
}

@media (max-width: 540px) {
    .ItemCard {
        width: 300px;
    }
    .image {
        width: 205px;
    }
}